// Configuration des contrles
export const CONTROLS = {
    LEFT: 'ArrowLeft',
    RIGHT: 'ArrowRight',
    DOWN: 'ArrowDown',
    ROTATE: 'ArrowUp',
    ROTATE_REVERSE: 'KeyZ',
    HARD_DROP: 'Space',
    HOLD: 'KeyC',
    PAUSE: 'KeyP'
};

// tat des touches - systme optimis avec sensibilit ajustable
const keys: Record<string, boolean> = {};
const lastMoveTime: { left: number; right: number; down: number } = { left: 0, right: 0, down: 0 };
export let MOVE_DELAY = 30; // Dlai minimum entre deux mouvements (ms) - ajustable

const CONTROL_KEY_SET = new Set([
    CONTROLS.LEFT,
    CONTROLS.RIGHT,
    CONTROLS.DOWN,
    CONTROLS.ROTATE,
    CONTROLS.ROTATE_REVERSE,
    CONTROLS.HARD_DROP,
    CONTROLS.HOLD,
    CONTROLS.PAUSE,
    'Enter'
]);

export interface GameControls {
    paused: boolean;
    gameOver: boolean;
    started: boolean;
    movePiece(dx: number): boolean;
    dropPiece(isManual: boolean): boolean;
    rotatePiece(): boolean;
    rotatePieceReverse(): boolean;
    hardDrop(): void;
    holdCurrentPiece(): void;
    togglePause(): void;
}

// Initialisation des contrles
export function initControls(game: GameControls): void {
    // vnements clavier
    document.addEventListener('keydown', (event) => {
        if (CONTROL_KEY_SET.has(event.code)) {
            event.preventDefault();
        }

        // Marquer la touche comme presse
        if (!keys[event.code]) {
            keys[event.code] = true;
        }

        // Grer l'input immdiatement
        handleInput(game, event.code);
    });

    document.addEventListener('keyup', (event) => {
        keys[event.code] = false;
    });

    // Boucle de rptition ultra-rapide pour les mouvements continus
    const continuousLoop = () => {
        handleContinuousInput(game);
        requestAnimationFrame(continuousLoop);
    };
    continuousLoop();
}

// Gestion des inputs ponctuels (premire pression uniquement)
function handleInput(game: GameControls, key: string): void {
    if (game.paused || game.gameOver) return;

    switch (key) {
        case CONTROLS.LEFT:
        case CONTROLS.RIGHT:
        case CONTROLS.DOWN:
            // Ces touches sont gres par handleContinuousInput pour viter la duplication
            break;
        case CONTROLS.ROTATE:
            game.rotatePiece();
            break;
        case CONTROLS.ROTATE_REVERSE:
            game.rotatePieceReverse();
            break;
        case CONTROLS.HARD_DROP:
        case 'Enter':
            game.hardDrop();
            break;
        case CONTROLS.HOLD:
            game.holdCurrentPiece();
            break;
        case CONTROLS.PAUSE:
            game.togglePause();
            break;
    }
}

// Gestion des inputs continus (rptition automatique quand touche maintenue)
function handleContinuousInput(game: GameControls): void {
    if (game.paused || game.gameOver || !game.started) return;

    const now = Date.now();

    // Gauche - rptition continue si maintenu
    if (keys[CONTROLS.LEFT]) {
        if (now - lastMoveTime.left >= MOVE_DELAY) {
            game.movePiece(-1);
            lastMoveTime.left = now;
        }
    }

    // Droite - rptition continue si maintenu
    if (keys[CONTROLS.RIGHT]) {
        if (now - lastMoveTime.right >= MOVE_DELAY) {
            game.movePiece(1);
            lastMoveTime.right = now;
        }
    }

    // Bas - rptition continue si maintenu
    if (keys[CONTROLS.DOWN]) {
        if (now - lastMoveTime.down >= MOVE_DELAY) {
            game.dropPiece(true);
            lastMoveTime.down = now;
        }
    }
}

// Fonction pour changer la sensibilit
export function setSensitivity(value: number): void {
    MOVE_DELAY = parseInt(String(value), 10);
    localStorage.setItem('tetris-sensitivity', String(value));
    console.log(`Sensibilit ajuste  ${MOVE_DELAY}ms`);
}

// Initialiser le contrle de sensibilit
export function initSensitivityControl(): void {
    const slider = document.getElementById('sensitivity-range');
    const valueDisplay = document.getElementById('sensitivity-value');
    const presetButtons = document.querySelectorAll('.preset-btn');

    // Charger la sensibilit sauvegarde
    const savedSensitivity = localStorage.getItem('tetris-sensitivity');
    if (savedSensitivity) {
        MOVE_DELAY = parseInt(savedSensitivity, 10);
        if (slider) (slider as HTMLInputElement).value = savedSensitivity;
        if (valueDisplay) valueDisplay.textContent = `${savedSensitivity}ms`;
    }

    // vnement du slider
    if (slider) {
        slider.addEventListener('input', (e) => {
            const value = (e.target as HTMLInputElement).value;
            setSensitivity(parseInt(value, 10));
            if (valueDisplay) {
                valueDisplay.textContent = `${value}ms`;
            }

            // Mettre  jour les boutons presets
            presetButtons.forEach(btn => {
                btn.classList.remove('active');
                if (btn.getAttribute('data-value') === value) {
                    btn.classList.add('active');
                }
            });
        });
    }

    // vnements des boutons presets
    presetButtons.forEach(btn => {
        btn.addEventListener('click', () => {
            const value = btn.getAttribute('data-value');
            if (value) {
                setSensitivity(parseInt(value, 10));

                if (slider) (slider as HTMLInputElement).value = value;
                if (valueDisplay) valueDisplay.textContent = `${value}ms`;

                // Mettre  jour l'tat actif
                presetButtons.forEach(b => b.classList.remove('active'));
                btn.classList.add('active');

                // Notification visuelle
                if (typeof window !== 'undefined' && (window as any).showNotification) {
                    (window as any).showNotification(`Sensibilit: ${btn.textContent}`);
                }
            }
        });
    });

    // Mettre  jour l'tat actif initial
    presetButtons.forEach(btn => {
        if (btn.getAttribute('data-value') === String(MOVE_DELAY)) {
            btn.classList.add('active');
        }
    });
}
